<template>
  <nav class="px-[1rem] border-b">
    <div class="flex justify-between items-center h-[4rem]">
      <div class="h-full flex">
        <div class="cont-logo px-2">
          <img :src="`http://67.205.184.183:3000/logos/${logo}`" alt="">
        </div>
        <div class="icono-menu p-4 cursor-pointer h-full flex items-center hover:bg-gray-50" @click="clickHambuger">
          <i class="fa-solid fa-bars"></i>
        </div>
      </div>
      <div class="flex items-center gap-2 lg:pr-5">
        <div class="font-medium text-sm dark:text-gray">
          <div>{{ username }}</div>
        </div>
        <i class="fa-solid fa-circle-user fa-xl"></i>
        <div class="rounded-full px-3 py-2 cursor-pointer hover:bg-gray-300" @click="togglePopup">
          <i class="fa-solid fa-ellipsis-vertical"></i>
        </div>
      </div>
    </div>
  </nav>
  <PopupSettings v-if="showPopup" class="popup" @logout-sesion="logout" />
  <div class="overlay" v-if="logoutProcessing">
    <span class="loader"></span>
  </div>
</template>

<script>
import PopupSettings from '@/components/generales/PopupSettings.vue';

export default {
  props: {
    dataOpenSideBar: Boolean,
    clickHambuger: Function,
    username: String,
    logo: String
  },
  components: {
    PopupSettings
  },
  methods: {
    togglePopup() {
      this.showPopup = !this.showPopup;
    },
    logout() {
      this.logoutProcessing = true;
      localStorage.clear();
      location.reload();
    },
  },
  data() {
    return {
      showPopup: false,
      logoutProcessing: false
    };
  }
}
</script>



<style lang="scss" scoped>
