  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 200px;
  padding: 4px 0;

  img {
    height: 100%;
    min-width: 100px;
  }
}

.popup {
  position: fixed;
  top: 60px;
  right: 3rem;
  width: 10rem;
  min-height: 5rem;
  background-color: white;
  border-radius: 4px;
  z-index: 9999;
  box-shadow: 0px 0px 17px -10px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: 0px 0px 17px -10px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 17px -10px rgba(0, 0, 0, 0.75);
