<template>
    <div class="min-w-[3rem] rounded-lg p-3">
        <button class=" text-gray-700 py-2 px-2 text-xs rounded-lg w-full text-left hover:bg-gray-200">
            <i class="fa-solid fa-user mr-2"></i> Perfil
        </button>
        <button class=" text-gray-700 py-2 px-2 text-xs rounded-lg w-full text-left  hover:bg-gray-200" @click="logout">
            <i class="fa-solid fa-right-from-bracket mr-2"></i> Cerrar sesión
        </button>
    </div>
</template>
<script>
export default({
    name:"PopupSettings",
    emits: ['logout-sesion'],
    setup(_,{ emit }){
        const logout = () => {
            emit('logout-sesion')
        }
        return {logout}
    }
})
</script>
