<template>
    <div class="fixed bottom-0 left-0 w-full bg-gray-200 border-t border-gray-300 z-[9900]">
        <div class="flex justify-around items-center py-1">
            <NavItemMobile :item="item" v-for="item in navItems" :key="item.label"/> 
        </div>
    </div>
</template>
<script>
import { ref } from 'vue';
import store from '@/store';
import NavItemMobile from '@/components/generales/NavItemMobile.vue';


export default ({
    name: "NavMobile",
    components:{
        NavItemMobile
    },
    setup() {
        const navItems = ref([]);
        if (store.state.codempresa == "C000" && store.state.tipousuario == 1) {
            navItems.value = [
                {
                    to: "/cuentas",
                    label: "Cuentas",
                    children: [],
                    icon: "fa-solid fa-user"
                }
            ]
        } else {
            navItems.value = [
                {
                    to: "/",
                    label: "Dashboard",
                    children: [],
                    icon: "fa-solid fa-chart-line"
                },
                {
                    to: "/reportes",
                    label: "Reportes",
                    children: [],
                    icon: "fa-solid fa-chart-simple"
                },
                {
                    to: "/unidades",
                    label: "Cisternas",
                    children: [],
                    icon: "fa-solid fa-truck-field-un"
                },
                {
                    to: "/clientes",
                    label: "Clientes",
                    children: [],
                    icon: "fa-solid fa-users"
                },
                {
                    to: "/conductores",
                    label: "Conductores",
                    children: [],
                    icon: "fa-solid fa-id-card"
                }
            ]
        }

        return { navItems };
    },
})
</script>